*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    width: 100%;
    margin-top: 1rem;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: aqua;
    background-color: whitesmoke;
}

.booklist{
    /* object-fit: cover; */
    width: 80vw;
    /* max-width: 1100px; */
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:1rem;
    margin: auto;
    /* margin-top: 1rem; */
    /* background:linear-gradient(to right, gray,white); */
}

.book{
    /* width: 100%; */
    /* margin: 1rem; */
    padding: 1rem;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 0.5rem;
    background: linear-gradient(to right, rgb(255, 255, 255),rgb(67, 134, 235));
    position: relative;
}
.book img{
    /* margin: 1rem; */
    margin: 1rem;
    max-width: 60%;
    object-fit: cover;
    
}

@media screen and (max-width:768px){
    .booklist{
        display: grid;
        grid-template-columns: 1fr;
    }
}

.number{
    position:absolute;
    top:0;
    left: 0;
    padding: 0.75rem;
    font-size: 1.75rem;
    color: white;
    background-color: blueviolet;
    /* border-top-left-radius: 2rem;
    border-bottom-right-radius: 2rem; */
    border-radius: 1rem;
}